.header {
  @extend %flex-center-ja;
  width: 100%;
  height: 64px;

  background-color: #2b2d33;
}

.header .container {
  @extend %flex-center-ja;
  gap: 86px;
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 80px;
}

.header .logo {
  margin-right: auto;
}

.nav__list {
  display: flex;
  gap: 86px;
}

.nav__link {
  font-size: 16px;
  color: #fff;
  text-decoration: none;
}

.search__input {
  display: flex;
  padding: 10px 16px;
  align-items: center;
  width: 360px;
  height: 44px;
  gap: 260px;

  color: #fff;
  font-size: 14px;
  line-height: normal;

  background: #464646;
}
.search__label {
  position: relative;
}
.search__label::before {
  content: '';
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);

  display: block;
  width: 24px;
  height: 24px;

  background-image: url('/static/images/loop.svg');
}

.burger-menu {
  background-color: transparent;
}