@import '/src/common/scss/common.scss';
@import '/src/components/logo/logo.scss';
@import '/src/components/header/header.scss';

.movie-details {
  padding: 48px 0;
}
.movie-details__container {
  display: flex;
  flex-direction: column;
  gap: 16px;
  max-width: 1440px;
  padding: 0 80px;
  margin: 0 auto;
}
.movie-details__box {
  display: flex;
}
.movie-details__box:nth-of-type(1) { gap: 32px; }
.movie-details__box:nth-of-type(2) { gap: 8px; }
.movie-details__box:nth-of-type(3) { gap: 8px; }
.movie-details__box:nth-of-type(4) { gap: 8px; margin-top: 32px;}

.movie-details__box-inner {
  display: flex;
  flex-direction: column;
}
.movie-details__box-inner:nth-of-type(1) { width: 318px; flex: 0 0 318px }
.movie-details__box-inner:nth-of-type(2) { flex-grow: 1;}

.details__list {
  display: flex;
  flex-direction: column;
  gap: 4px;
}
.details__item {
  display: flex;
  gap: 24px;
}
.details__key {
  flex: 0 0 85px;
  width: 85px;

  color: #B1AAAA;
  font-size: 14px;
  line-height: 140%;
}
.details__value {
  flex-grow: 1;
  color: #FFF;
  font-size: 14px;
  line-height: 140%;
}

.movie-details__ratings {
  display: flex;
  gap: 32px;
  margin-bottom: 16px;
}

.movie-details__ratings p:nth-of-type(1) {
  display: flex;
  padding: 6px;
  justify-content: center;
  align-items: center;
  gap: 6px;

  color:  #F2FF5E;
  
  font-size: 14px;
  line-height: 140%;
}
.movie-details__ratings p:nth-of-type(2),
.movie-details__ratings p:nth-of-type(3) {
  display: flex;
  align-items: center;
  gap: 8px;

  color: #fff;
  font-size: 16px;
  font-weight: 700;
  line-height: 140%;

  & span {
    color: #B1AAAA;
    font-size: 14px;
    line-height: 140%;
  }
}
.like__list {
  display: flex;
  gap: 16px;
  margin-left: auto;
}
.like__btn {
  background-color: transparent;

  transition: 300ms linear;
  &:hover, &:focus {scale: 1.04}
}
.plot__label {
  margin-bottom: 16px;

  color: #FFF;
  font-size: 20px;
  font-weight: 500;
  line-height: normal;
}
.plot__text {
  overflow: hidden;
  color: #FFF;
  text-overflow: ellipsis;
  whitespace: nowrap;
  font-size: 16px;
  line-height: 140%;
}

.actors__list {
  display: flex;
  flex-wrap: wrap;
  column-gap: 12px;
  row-gap: 8px;
  height: 556px;
  overflow: hidden;
}
.actors__item {
  flex: 0 0 145px;
  width: 145px;
}
.actors__img {
  object-fit: cover;
}
.actors__desc {
  display: flex;
  width: 145px;
  padding: 8px 0px 8px 16px;
  flex-direction: column;
  align-items: flex-start;
  gap: 4px;

  background-color: #2B2D33;
}
.actors__name {
  color: #fff;
  
  font-size: 12px;
  line-height: 130%;
}
.actors__role {
  color: #B1AAAA;
  
  font-size: 12px;
  line-height: 130%;
}

.comments {
  position: relative;
  padding: 0 48px;
  height: 557px;
  overflow: hidden;
}
.comments__label {
  margin-bottom: 16px;

  color: #FFF;
  font-size: 16px;
  font-weight: 700;
  line-height: 140%;
}
.comment__add-new {
  display: flex;
  gap: 8px;
}
.comment__img {
  flex: 0 0 48px;
  object-fit: cover;
}

.comment__form {
  position: relative;
  display: flex;
  // height: 50px;
  
  align-items: center;
  flex: 1 0 0;
  margin-bottom: 40px;


  background-color: #212328;

  border: 1px solid #FFF;
}
.comment-form__input {
  flex: 1 0 600px;
  height: 50px;
  padding: 8px 16px;

  // width: 100%;
  color: #B1AAAA;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 130%;
  background-color: #212328;
}
.comment-form__btn {
  position: absolute;
  // top: 0px;
  right: 0;

  display: flex;
  height: 48px;
  padding: 8px 32px;
  justify-content: center;
  align-items: center;
  gap: 8px;

  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 140%;
  color: #fff;
  background-color: #2B2D33;

  transition: 300ms linear;
  &:hover, &:focus {background: #464646;}
}
.comment-form__btn svg {
  flex: 0 0 24px;
  width: 24px;
  height: 24px;
}

.comments__amount {
  display: flex;
  align-items: center;
  gap: 8px;
  width: 130px;
  height: 22px;
  margin-bottom: 24px;

  color: #FFF;
  font-size: 16px;
  font-weight: 700;
  line-height: 140%;

  background-color: transparent;
}
.comments__list {
  display: flex;
  flex-direction: column;
  gap: 24px;
}
.comments__item {
  display: flex;
  gap: 16px;
  height: 118px;
}
.comment__desc {
  display: flex;
  flex-direction: column;
  gap: 8px;
}
.comment-like__list {
  display: flex;
  gap: 16px;
}
.comment-like__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
}
.comment-like__item span {
  color: #B7A8A8;

  font-size: 14px;
  line-height: 140%
}

.comments__blur {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 50px;

  background: rgba(33, 35, 40, 0.80);
  backdrop-filter: blur(1.5px);
  z-index: 1;
}


.movie-details__desc-wrap {
  margin-right: auto;
}
.movie-details__title {
  margin-bottom: 8px;

  color: #FFF;
  font-size: 24px;
  font-weight: 500;
  line-height: 140%;
}
.movie-details__desc {
  color: #A8A8A8;
  font-size: 16px;
  line-height: 140%; 
}
.movie-details__btn {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 4px;
  height: 50px;
  padding: 8px 12px;

  color: #FFF;
  font-size: 16px;
  font-weight: 400;
  line-height: 140%;

  background: #2B2D33;
  transition: 300ms linear;

  &:hover, &:focus {background: #464646;}
}
.movie-details__img {
  object-fit: cover;
  
  &:last-of-type { cursor: pointer; }
}



.movies.section {
  min-height: 100%;
  padding: 0 0 140px;

  background-color: #212328;
  overflow: hidden;
}
.movies__container {
  display: flex;
  flex-direction: column;
  gap: 32px;
  max-width: 1440px;
  padding: 0 80px;
  margin: 0 auto;
}
.filters {
  display: flex;
  flex-direction: column;
  gap: 36px;
}
.filters__box {
  display: flex;
  gap: 16px;
}
.filters-category__list {
  display: flex;
  gap: 40px;
}
.filters-category__btn {
  position: relative;
  color: #fff;
  font-weight: 700;
  line-height: normal;

  background-color: transparent;
}
.filters-category__btn.current::before {
  content: '';
  position: absolute;
  bottom: -4px;

  display: block;
  width: 100%;
  height: 1px;

  background: #fff;
}

.filters-dropdown__list {
  position: relative;
  display: flex;
  flex-direction: column;

  border: 1px solid #fff;
}
.filters-dropdown__list::before {
  content: '';
  position: absolute;
  top: 8px;
  right: 16px;

  display: block;
  width: 16px;
  height: 16px;
  transition: 300ms linear;

  background-image: url('/static/images/filter-arrow.svg');
}
.filters-dropdown__list.isActive::before {
  // display: none;
  transform: rotate(180deg);
}
.filters-dropdown__item {
  display: flex;
  width: 100%;
  padding: 8px 16px;
  align-items: center;

  font-size: 12px;
  line-height: normal;
  color: #fff;

  .filters-dropdown__list.isActive &:not(:first-of-type) {
    border-top: 1px solid #fff;
  }
  &.isHidden {
    display: none;
  }

  &:hover {background: #464646;}
}

.filters-genre {
  position: relative;
  width: 200px;
  min-height: 32px;
}
.filters-year {
  position: relative;
  width: 92px;
  min-height: 32px;
}
.filters-country {
  position: relative;
  width: 184px;
  min-height: 32px;
}
.filters-perpage {
  position: relative;
  width: 48px;
  min-height: 32px;
}


.filters-genre__list {
  position: absolute;
  width: 200px;

  background-color: #212328;
  z-index: 2;
}
.filters-year__list {
  position: absolute;
  width: 92px;

  background-color: #212328;
  z-index: 2;
}
.filters-country__list {
  position: absolute;
  width: 184px;

  background-color: #212328;
  z-index: 2;
}
.filters-perpage__list {
  position: absolute;
  width: 48px;

  background-color: #212328;
  z-index: 2;
}
.filters-perpage__list.filters-dropdown__list::before {
  right: 8px;
}
.filters-perpage__item.filters-dropdown__item {
  padding: 8px 24px 8px 8px;
}

.filters-perpage {
  margin-left: auto;
}

.movies__list {
  display: flex;
  // justify-content: center;
  // flex-wrap: wrap;
  --gap: 16px;
  gap: var(--gap);
}
.movies__item {
  --items: 6;
  flex-shrink: 0;
  flex-basis: calc((100% - var(--gap) * (var(--items) - 1)) / var(--items));
}
.movie {
  position: relative;
  cursor: pointer;

  &::before {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
  
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 1;
    opacity: 0;
    transition: all 300ms ease-out;
  }
}
.movie:hover::before,
.movie:focus::before { opacity: 1;}

.movie__img-wrap {
  position: relative;
}
.movie__img-wrap::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  display: block;
  width: 72px;
  height: 72px;

  background-image: url('/static/images/play.svg');
  transition: all 300ms ease-out;
  z-index: 1;
  opacity: 0;
}
.movie:hover .movie__img-wrap::before,
.movie:focus .movie__img-wrap::before { opacity: 1;}


.movie__img {
  width: 100%;
  height: auto;
  object-fit: cover;
}
.movie__viewing,
.movie__rating {
  position: absolute;
  top: 16px;

  display: flex;
  width: 77px;
  height: 24px;
  padding: 4px 16px 4px 8px;
  justify-content: center;
  align-items: center;
  gap: 10px;

  color: #fff;
  font-size: 14px;
  line-height: normal;

  background: #464646;
}

.movie__viewing {
  border-radius: 0px 16px 16px 0px;
}
.movie__rating {
  right: 0;
  border-radius: 16px 0px 0px 16px;
}
.movie__btn {
  position: absolute;
  bottom: 16px;

  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;

  color: #fff;

  border-radius: 50%;
  background: #464646;
}
.movie__btn-card {
  left: 16px;
}
.movie__btn-users {
  left: calc(16px + 8px + 40px);
}
.movie__desc {
  padding: 8px 16px;
}
.movie__title {
  margin-bottom: 8px;

  color: #fff;
  font-size: 16px;
  font-weight: 500;
  line-height: normal;
}

.movie__year,
.movie__country {
  color: #a8a8a8;
  font-size: 14px;
  font-weight: 400;
  line-height: normal;
}

.movie__year::after {
  content: ',';
  color: #a8a8a8;
  font-size: 14px;
  font-weight: 400;
  line-height: normal;
}
