@import './reset';
@import './var';
// utils styles
@import './utils/mixins';
@import './utils/placeholders';
@import './utils/visually-hidden.scss';

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;

  font-size: 16px;
  line-height: 1.2;
  font-family: var(--font--body);
  color: #fff;
  background-color: #212328;
}
main {
  flex-grow: 1;
}
footer {
  margin-top: auto;
}

svg {
  fill: currentColor;
  pointer-events: none;
}
button, a {
  @extend %cursor;
}